	module.exports = {"data":{"page":{"id":"/home/travis/build/benmvp/benmvp.github.io/content/pages/01--about.md absPath of file >>> MarkdownRemark","html":"<p>Ben Ilegbodu is a Jesus follower (aka a <a href=\"http://www.aweandreverence.com/gospel/what-is-the-gospel/248\">Christian</a>), a husband, and a father of 2. He lives in Pittsburg, California with his wife Rashida and their daughters Simone and Avery.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/family-house-selfie-d81278a5e3aa93443e21895fc8eac49b-163b3.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\"\n        alt=\"Ilegbodu Family\"\n        title=\"\"\n        src=\"/static/family-house-selfie-d81278a5e3aa93443e21895fc8eac49b-78f2b.jpg\"\n        srcset=\"/static/family-house-selfie-d81278a5e3aa93443e21895fc8eac49b-dce19.jpg 200w,\n/static/family-house-selfie-d81278a5e3aa93443e21895fc8eac49b-c1413.jpg 400w,\n/static/family-house-selfie-d81278a5e3aa93443e21895fc8eac49b-78f2b.jpg 800w,\n/static/family-house-selfie-d81278a5e3aa93443e21895fc8eac49b-ab4c4.jpg 1200w,\n/static/family-house-selfie-d81278a5e3aa93443e21895fc8eac49b-bc99b.jpg 1600w,\n/static/family-house-selfie-d81278a5e3aa93443e21895fc8eac49b-34a60.jpg 2400w,\n/static/family-house-selfie-d81278a5e3aa93443e21895fc8eac49b-163b3.jpg 2592w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Ben is currently a Senior UI Engineer and manager of the Frontend Platform team at <a href=\"https://www.eventbrite.com/\">Eventbrite</a> (<a href=\"https://twitter.com/eventbrite\">@Eventbrite</a>). He originally joined the Listings team at Eventbrite in May 2015, tasked to revamp the event details pages from an archaic MySpace-style design to a modern, fully responsive layout.</p>\n<p>Prior to Eventbrite, Ben spent nearly 9 years at <a href=\"http://www.zazzle.com\">Zazzle</a> (<a href=\"https://twitter.com/zazzle\">@Zazzle</a>) where he started out as an entry-level UI Engineer straight out of college and grew to eventually become its UI Architect. At Zazzle, Ben implemented multiple iterations of the product details pages and design tools as well as architected and developed Zazzle’s server- and client-side UI framework.</p>\n<p>Ben attended <a href=\"%5B@Stanford(http://twitter.com/stanford)%5D\">Stanford University</a> and received both a B.S. and M.S. in Computer Science with an HCI concentration. His strong computer science background, coupled with his HCI training and passion for building web sites, has provided him with a unique set of skills where he is able to get nitty gritty with hardcore back-end developers as well as work through challenging UX issues with designers and product managers.</p>\n<p>Ben’s main focus is in building rich user interactions on the web. He’s having fun learning &#x26; playing around with <a href=\"https://facebook.github.io/react/\">React</a> and its ecosystem.</p>\n<p>Ben’s complete resume is available on <a href=\"https://www.linkedin.com/in/benmvp\">LinkedIn</a>. Follow him on Twitter at <a href=\"http://twitter.com/benmvp\">@BenMVP</a>. Fork him on <a href=\"https://github.com/benmvp\">Github</a>. Track his <a href=\"/speak/\">speaking engagements</a>. Watch his <a href=\"/videos/\">speaking videos</a>. <a href=\"/ama\">Ask him anything</a>!</p>","frontmatter":{"title":"About Ben"}},"footnote":{"id":"/home/travis/build/benmvp/benmvp.github.io/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>© Ben Ilegbodu 2018.</li>\n<li>Powered by <a href=\"https://www.gatsbyjs.org/\">Gatsby</a> &#x26; <a href=\"https://reactjs.org\">React</a> using <a href=\"https://github.com/greglobinski/gatsby-starter-personal-blog\">gatsby-starter-personal-blog</a>.</li>\n</ul>"}},"pathContext":{"slug":"/about/"}}

